import React, { Component } from 'react';
import './BodyContainer.css';
import { ProfileContainer, GitHubContainer, ProjectContainer } from '../../containers';
import { connect } from 'react-redux';

class BodyContainer extends Component {
    render() {
        let displayType = this.props.display;
        return (
            <div className="bodyContents">
                <BodyContents displayType={displayType} />
            </div>
        );
    }
}

function BodyContents(props) {
    let displayType = props.displayType;
    
    if (displayType === 'aboutMe') {
        return <ProfileContainer />;
    } else if (displayType === 'gitHub') {
        return  <GitHubContainer />;
    } else if (displayType === 'tistory') {
        return  <ProfileContainer />;            
    } else if (displayType === 'projects') {
        return  <ProjectContainer />;
    } else {
        return null;
    }
}

let mapStateToProps = (state) => {
    return {
        display: state.displayReducer.display
    }
}

BodyContainer = connect(mapStateToProps)(BodyContainer);

export default BodyContainer;


