import React, { Component } from 'react';
import './App.css';

import { HeaderContainer, BodyContainer, FooterContainer } from './containers';

class App extends Component {
  render() {
    return (
      <div>
        <HeaderContainer />
        <BodyContainer />
        <FooterContainer />
      </div>
    );
  }
}

export default App;



