import React from 'react';
import './GitHubGraph.css';
import ReactHighcharts from 'react-highcharts';
import Highcharts from 'highcharts';
import Drilldown from 'highcharts-drilldown';
Drilldown(Highcharts);

function getConfig(config, drilldown) {
    return {
        chart: {
            type: 'column'
        },
        title: {
            text: ''
        },
        xAxis: {
            type: 'category'
        },
        yAxis: {
            title: {
                text: '  '
            },
            tickInterval: 1
        },
        legend: {
            enabled: false
        },
        plotOptions: {
            series: {
                borderWidth: 0,
                dataLabels: {
                    enabled: true,
                    format: '{point.y}'
                }
            }
        },
        tooltip: {
            headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
            pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y}</b><br/>'
        },
        series: [{
            name: 'Language',
            colorByPoint: true,
            data: config
        }],   
        credits: {
            enabled: false
        }
    }
}

const GitHubGraph = (props) => {
    let config = props.config;
    let drilldown = props.drilldown;
    return (
        <div className="gitHubGraphWapper">
            <ReactHighcharts config={getConfig(config, drilldown)}></ReactHighcharts>
        </div>
    );
};

export default GitHubGraph;


