import React from 'react';
import { Checkbox } from '../../components';
import './ListItem.css';

const ListItem = (props) => {
    let active = props.active === undefined ? '' : ' ' + props.active;
    return (
        <li id={props.id} className={`listItem ${active}`} onClick={props.onClick}>
            <div className="liTitle">
                <Checkbox active={props.active} />
                <span>{props.title}</span>
                {props.url === '#'
                    ? <a className="projectLink" href="#">#</a>
                    : <a className="projectLink" href={props.url} target="_blank">#</a>}
            </div>
            {props.children}
            <div className="liContent">
                <span>{props.description}</span>
            </div>
        </li>
    );
};

export default ListItem;


