import React from 'react';
import './ListItem.css';
import * as Config from '../../Config.js';

const Tags = (props) => {
    if (props.tags !== undefined && props.tags.length !== 0) {
        const tags = props.tags.map(tag => <Tag key={tag} tag={tag} /> );
        return <span>{tags}</span>;
    } else {
        return null;
    }
}

const Tag = (props) => {
    let tag = props.tag;
    return (
        <small>
            &nbsp;<a href={`${Config.TISTORY_URL}/tag/${tag}`} target="_blank" className="tagName">{`#${tag}`}</a>
        </small>
    );
}

export default Tags;


