import React from 'react';
import { ListItem, Tags, Checkbox } from '../../components';
import './ProjectText.css';

const ProjectText = (props) => {
    return (
        <div className="projectTextWapper">
            <ul className="projectList">
                {props.projects.map(item => {
                    let active = props.activeList.indexOf(item._id) >= 0 ? 'on' : '';
                    let url = item.url === undefined || item.url === null || item.url === '' ? '#' : item.url;
                    return (
                        <ListItem key={item._id} id={item._id} active={active} onClick={props.onClick}
                            title={item.title} url={url} description={item.description} >
                            <div className="frontEndTags">
                                <small>Front End :</small><Tags tags={item.frontEnd} />
                            </div>
                            <div className="backEndTags">
                                <small>Back End :</small><Tags tags={item.backEnd} />
                            </div>
                        </ListItem>
                    );
                })}
            </ul>
        </div>
    );
};

export default ProjectText;


