import React, { Component } from 'react';
import './HeaderContainer.css';
import { Header } from '../../components';
import { connect } from 'react-redux';
import { display } from '../../actions';

class HeaderContainer extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(event) {
        this.props.setDisplayType(event.target.name);
    }

    render() {
        return (
            <div className="headContents">
                <Header active={this.props.display} onClick={this.handleClick}/>
            </div>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        display: state.displayReducer.display
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        setDisplayType: (value) => dispatch(display(value))
    }
}

HeaderContainer = connect(mapStateToProps, mapDispatchToProps)(HeaderContainer);

export default HeaderContainer;


