import React from 'react';
import { ListItem, Tags, Checkbox } from '../../components';
import './GitHubText.css';

const GitHubText = (props) => {
    return (
        <div className="gitHubTextWapper">
            <ul className="repoList">
                {props.repos.map(item => {
                    let active = props.activeList.indexOf(item.id) >= 0 ? 'on' : '';                    
                    let url = item.url === undefined || item.url === null || item.url === '' ? '#' : item.url;
                    return (
                        <ListItem key={item.id} id={item.id} active={active} onClick={props.onClick}
                            title={item.title} url={url} description={item.description} >
                            <Tags tags={item.tags} />
                        </ListItem>
                    );
                })}
            </ul>
        </div>
    );
};

export default GitHubText;


