import React, { Component } from 'react';
import './GitHubContainer.css';
import { getRepositoryList } from '../../services/github';
import { GitHubText, GitHubGraph } from '../../components';
import { connect } from 'react-redux';
import { setGitHubInfo, setGitHubActiveList } from '../../actions';

class GitHubContainer extends Component {
    componentDidMount() {
        this.fetchRepositoryList();
    }

    fetchRepositoryList = async () => {
        if (this.props.repos.length === 0) {
            let repos = await getRepositoryList();
            let mining = miningRepoList(repos.data);
            this.props.setGitHubInfo(mining, mining.map((item) => item.id));
        }
    }

    handleClick = (event) => {
        let id = Number(event.currentTarget.id);
        let activeList = this.props.activeList.map((item) => item);
        let index = activeList.indexOf(id);
        if (index >= 0) {
            activeList.splice(index, 1);
        } else {
            activeList.push(id);
        }
        this.props.setActiveList(activeList);
    }    

    render() {
        let repos = this.props.repos;
        let activeList = this.props.activeList;
        let activeRepos = repos.filter((item) => { return activeList.indexOf(item.id) >= 0; });
        console.log("[hb]", activeList);

        if (repos.length === 0) {
            return <div className="gitHubWapper animated fadeIn"></div>;
        }
        else {
            let graphConfig = getGitHubConfig(activeRepos);
            return (
                <div className="gitHubWapper animated fadeIn">
                    <GitHubGraph config={graphConfig} />
                    <GitHubText repos={repos} activeList={activeList} onClick={this.handleClick} />
                </div>
            );
        }
    }
}

function getGitHubConfig(miningRepos) {
    let graphInfo = [];
    for (let item of miningRepos) {
        let tags = item.tags;
        for (let tag of tags) {
            let data = {
                name: tag,
                y: 1,
            };
            let temp = graphInfo.find(x => x.name === data.name);
            if (temp === undefined) {
                graphInfo.push(data);
            } else {
                temp["y"] = Number(temp["y"]) + 1;
            }
        }
    }

    return graphInfo.sort((a, b) => {return b.y - a.y;});
}

function miningRepoList(repos) {
    return repos.map(repo => {
        let rawData = repo.description;
        let description = '';
        let tags = [];

        if (rawData !== null) {
            let splitData = rawData.split('] ');
            if (splitData.length > 1) {
                description = splitData[1];
                let rawTags = splitData[0].replace('[', '').replace(']', '').trim();
                tags = tags.concat(rawTags.split(', '));
            }
        }

        return { id:repo.id
            , title: repo.name
            , description: description
            , url: repo.svn_url
            , tags: tags
        };
    });
}

let mapStateToProps = (state) => {
    return {
        repos: state.gitHubReducer.repos,
        activeList: state.gitHubReducer.activeList,
    };
}

let mapDispatchToProps = (dispatch) => {
    return {
        setGitHubInfo: (repos, activeList) => dispatch(setGitHubInfo(repos, activeList)),
        setActiveList: (value) => dispatch(setGitHubActiveList(value))
    };
}

GitHubContainer = connect(mapStateToProps, mapDispatchToProps)(GitHubContainer);

export default GitHubContainer;


