import React from 'react';
import './ProfileText.css';

const ProfileText = (props) => {
    return (
        <div className="textArea">
            <p className="profileName">{props.name}</p>
            <p><small>{props.company}</small></p>
            <br />
            <p>      .</p>
            <p>   .</p>
        </div>
    );
};

export default ProfileText;


