import React from 'react';
import './ProjectGraph.css';
import ReactHighcharts from 'react-highcharts';

function getConfig(config, drilldown) {
    return {
        chart: {
            type: 'pie'
        },
        title: {
            text: ''
        },
        plotOptions: {
            series: {
                dataLabels: {
                    enabled: true,
                    format: '{point.name}: {point.y:.1f}%'
                }
            }
        },
        tooltip: {
            headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
            pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}%</b> of total<br/>'
        },
        series: [{
            name: 'Type',
            colorByPoint: true,
            data: config
        }],
        drilldown: {
            animation: false,
            series: drilldown
        },
        credits: {
            enabled: false
        }
    }
}

const ProjectGraph = (props) => {
    let config = props.config;
    let drilldown = props.drilldown;    
    return (
        <div className="projectGraphWapper">
            <ReactHighcharts config={getConfig(config, drilldown)}></ReactHighcharts>
        </div>
    );
};

export default ProjectGraph;


