import React, { Component } from 'react';
import { ProfileText, ProfileImage } from '../../components';
import { getProfileInfo } from '../../services/github';
import { connect } from 'react-redux';
import { setProfile } from '../../actions';
import './ProfileContainer.css';

class ProfileContainer extends Component {
    componentDidMount() {
        this.fetchProfileInfo();
    }

    fetchProfileInfo = async () => {
        if (Object.keys(this.props.profile).length === 0) {
            var profileInfo = await getProfileInfo();
            this.props.setProfile({
                imgSrc: profileInfo.data.avatar_url,
                name: profileInfo.data.name,
                company: profileInfo.data.bio
            });
        }
    }

    render() {
        var profile = this.props.profile;
        if (Object.keys(profile).length === 0) {
            return <div className="profileWapper animated fadeIn"></div>;
        } else {
            return (
                <div className="profileWapper animated fadeIn">
                    <ProfileImage img={profile.imgSrc} />
                    <ProfileText name={profile.name} company={profile.company} />
                </div>
            );
        }
    }
}

let mapStateToProps = (state) => {
    return {
        profile: state.profileReducer.profile
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        setProfile: (value) => dispatch(setProfile(value))
    }
}

ProfileContainer = connect(mapStateToProps, mapDispatchToProps)(ProfileContainer);

export default ProfileContainer;


