import React from 'react';
import './Header.css';

const Header = (props) => {
    let activeMenu = props.active;
    return (
        <div className="headerWapper">
            <div className="titleArea">
                <p>BEOMY. Blog</p>
            </div>
            <div className="buttonArea">
                <ul>
                    <li className={activeMenu === "aboutMe" ? "active" : ""}><button name="aboutMe" onClick={props.onClick}>About Me</button></li>
                    <li className={activeMenu === "gitHub" ? "active" : ""}><button name="gitHub" onClick={props.onClick}>GitHub</button></li>
                    {/*<li><button name="tistory" onClick={props.onClick}>Tistroy</button></li>*/}
                    <li className={activeMenu === "projects" ? "active" : ""}><button name="projects" onClick={props.onClick}>Projects</button></li>
                </ul>
            </div>
        </div>
    );
};

export default Header;


