import React, { Component } from 'react';
import './FooterContainer.css';
import { Footer } from '../../components'

class FooterContainer extends Component {
    render() {
        return (
            <div className="footContents">
                <Footer />
            </div>
        );
    }
}

export default FooterContainer;


