export const DISPLAY = 'DISPLAY';
export const SET_PROFILE = 'SET_PROFILE';
export const SET_GITHUB_INFO = 'SET_GITHUB_INFO';
export const SET_GITHUB_ACTIVE_LIST = 'SET_GITHUB_ACTIVE_LIST';
export const SET_PROJECT = 'SET_PROJECT';
export const SET_PROJECT_ACTIVE_LIST = 'SET_PROJECT_ACTIVE_LIST';

export function display(type) {
    return {
        type: DISPLAY,
        display: type
    };
}

export function setProfile(profile) {
    return {
        type: SET_PROFILE,
        profile: profile
    };
}

export function setGitHubInfo(repos, activeList) {
    return {
        type: SET_GITHUB_INFO,
        repos: repos,
        activeList: activeList
    };
}

export function setGitHubActiveList(activeList) {
    return {
        type: SET_GITHUB_ACTIVE_LIST,
        activeList: activeList
    };
}

export function setProject(projects, activeList) {
    return {
        type: SET_PROJECT,
        projects: projects,
        activeList: activeList
    };
}

export function setProjectActiveList(activeList) {
    return {
        type: SET_PROJECT_ACTIVE_LIST,
        activeList: activeList
    };
}



