import React, { Component } from 'react';
import { getProjects } from '../../services/project';
import { connect } from 'react-redux';
import { setProject, setProjectActiveList } from '../../actions';
import { ProjectText, ProjectGraph } from '../../components';
import './ProjectContainer.css';

class ProjectContainer extends Component {
    componentDidMount() {
        this.fetchProjects();
    }

    fetchProjects = async () => {
        if (this.props.projects.length === 0) {
            let projectList = await getProjects();
            let projects = projectList.data;
            this.props.setProject(projects, projects.map((item) => item._id));
        }
    }

    handleClick = (event) => {
        let id = event.currentTarget.id;
        let activeList = this.props.activeList.map((item) => item);
        let index = activeList.indexOf(id);
        if (index >= 0) {
            activeList.splice(index, 1);
        } else {
            activeList.push(id);
        }
        this.props.setActiveList(activeList);
    }

    render() {
        let projects = this.props.projects;
        let activeList = this.props.activeList;
        let activeProject = projects.filter((item) => { return activeList.indexOf(item._id) >= 0; });
        console.log("[hb]", activeList);

        if (Object.keys(projects).length === 0) {
            return <div className="projectWapper animated fadeIn"></div>;
        } else {
            let graphConfig = getProjectConfig(activeProject);
            let drilldownConfig = getProjectDrilldownConfig(activeProject);
            return (
                <div className="projectWapper animated fadeIn">
                    <ProjectGraph config={graphConfig} drilldown={drilldownConfig} />
                    <ProjectText projects={projects} activeList={activeList} onClick={this.handleClick} />
                </div>
            );
        }
    }
}

function getProjectConfig(projects) {
    let frontEnd = [];
    let backEnd = [];
    for (let item of projects) {
        frontEnd = frontEnd.concat(item.frontEnd);
        backEnd = backEnd.concat(item.backEnd);
    }
    frontEnd.sort((a, b) => {return b.y - a.y;});
    backEnd.sort((a, b) => {return b.y - a.y;});

    return [{
            name: 'Front End',
            y: frontEnd.length / (frontEnd.length + backEnd.length) * 100,
            drilldown: 'Front End'
        }, {
            name: 'Back End',
            y: backEnd.length / (frontEnd.length + backEnd.length) * 100,
            drilldown: 'Back End'
        }];
}

function getProjectDrilldownConfig(projects) {
    let frontEnd = [];
    let backEnd = [];
    for (let item of projects) {
        frontEnd = frontEnd.concat(item.frontEnd);
        backEnd = backEnd.concat(item.backEnd);
    }    
    let frontEndGroup = groupBy(frontEnd);
    let backEndGroup = groupBy(backEnd);

    let frontData = [];
    let backData = [];
    for (let key in frontEndGroup) {
        frontData.push([key,  Number((frontEndGroup[key] / frontEnd.length * 100).toFixed(1))]);
    }
    for (let key in backEndGroup) {
        backData.push([key, Number((backEndGroup[key] / backEnd.length * 100).toFixed(1))]);
    }
    frontData.sort((a, b) => {return b[1] - a[1];});
    backData.sort((a, b) => {return b[1] - a[1];});

    return [{
            name: 'Front End',
            id: 'Front End',
            data: frontData
        }, {
            name: 'Back End',
            id: 'Back End',
            data: backData
        }];
}

function groupBy(arr) {
    let hist = {};
    for (let a of arr) {
        if (a in hist) hist[a] ++; else hist[a] = 1;
    }
    return hist;
}

let mapStateToProps = (state) => {
    return {
        projects: state.projectReducer.projects,
        activeList: state.projectReducer.activeList
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        setProject: (projects, activeList) => dispatch(setProject(projects, activeList)),
        setActiveList: (value) => dispatch(setProjectActiveList(value))
    }
}

ProjectContainer = connect(mapStateToProps, mapDispatchToProps)(ProjectContainer);

export default ProjectContainer;


