import { DISPLAY, SET_PROFILE, SET_GITHUB_INFO, SET_PROJECT, SET_GITHUB_ACTIVE_LIST, SET_PROJECT_ACTIVE_LIST } from '../actions';
import { combineReducers } from 'redux';

const displayInitialState = {
    display: 'aboutMe'
};

const displayReducer = (state = displayInitialState, action) => {
    switch(action.type) {
        case DISPLAY:
            return Object.assign({}, state, {
                display: action.display
            });
        default:
            return state;
    }
};

const profileInitialState = {
    profile: {}
};

const profileReducer = (state = profileInitialState, action) => {
    switch(action.type) {
        case SET_PROFILE:
            return Object.assign({}, state, {
                profile: action.profile
            });
        default:
            return state;
    }
};

const reposInitialState = {
    repos: []
};

const gitHubReducer = (state = reposInitialState, action) => {
    switch(action.type) {
        case SET_GITHUB_INFO:
            return Object.assign({}, state, {
                repos: action.repos,
                activeList: action.activeList
            });
        case SET_GITHUB_ACTIVE_LIST:
            return Object.assign({}, state, {
                activeList: action.activeList
            });            
        default:
            return state;
    }
};

const projectsInitialState = {
    projects: [],
    activeList: []
};

const projectReducer = (state = projectsInitialState, action) => {
    switch(action.type) {
        case SET_PROJECT:
            return Object.assign({}, state, {
                projects: action.projects,
                activeList: action.activeList
            });
        case SET_PROJECT_ACTIVE_LIST:
            return Object.assign({}, state, {
                activeList: action.activeList
            });
        default:
            return state;
    }
};

const blogApp = combineReducers({
    displayReducer,
    profileReducer,
    gitHubReducer,
    projectReducer
});

export default blogApp;


